.button-group {
    position: relative;
display: inline-flex;
vertical-align: middle;
}



.chart-btn {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: 0.3rem 0.5rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    color: #fff;
    background-color: #7896d6;
    border-color: #7896d6;
    transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    margin: 20px 1px;
    cursor: pointer
}

.percent-change-display {
    font-size: 1.3rem;
    text-align: center;
}

.market-rank {
    background-color: black;
    color: white;
    padding: 4px 6px;
}

.name-div {
    display: flex;
    margin: 5px 0;
    align-items: center;
    transform: translateX(-20px)
}

.coin-image {
    border-radius: 50%;
    width: 50px;
    margin: 10px;
}

.crypto-name {
    font-size: 1.6rem;
    font-size: bold;
}

.c-info-symbol {
    font-size: 1.3rem;
    color: rgb(87, 87, 87);
    margin: 0 5px;
}

.progress-bar {
    min-width: 400px;
}

.current-price {
    font-size: 1.55rem;
}

.price-change-24 {
    font-size: 1.2rem;
    margin-left: 5px;
}

.crypto-info-container {
    padding: 3em 8rem;
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.grid-box {
    display: flex;
    flex-direction: column;
    margin-top: 25px;
}

.grid-box > span {
    border-bottom: 1px solid rgba(211, 211, 211, 0.65);
    padding: 12px 0 ;
    font-size: 1.1rem;
}

.converter-div {
    display: flex;
    align-items: baseline;
    margin: 20px 0;
    justify-content: center;
}

.convert-input {
    display: flex;
    align-items: center;
}

.input {
    width: 120px;
    height: 32px;
    margin: 0 2px ;
}
.chart-container {
    width: 70%;
    padding: 0 2em;
}
.fa-exchange {
    margin: 0 10px;
}

.active-btn {
    background-color: #d7e5ffc7 !important;
}

.description-div {
    font-size: 1.2em;
    margin: 1.125rem 0;
}

/* News styles  */

.main-news-wrapper {
    display: flex;
    padding: 1em 12em;
    justify-content: space-evenly;
}
.padding-container{
    padding: 1em 4em 0 0;
}

.badge-container {
    margin: 1em 0;
}


.tag-badge {
    background-color: rgb(27, 100, 160);
    padding: 4px 8px;
    border-radius: 100px;
    display: inline-block;
    margin: 3px;
    color: white;
}



