}
@media screen and (max-width: 970px) {
    .crypto-info-container {
        flex-direction: column;
        padding:20px;
        width: 100%;
    }
    .chart-container {
        width: 100%;
        padding: 0 3em;
    }
}



@media screen and (max-width: 600px) {
     .chart-container {
         padding: 0;
     }
}
