@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,700;1,100;1,700&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto';
}
body {
    overflow-x: hidden;
}
a {
    text-decoration: none;
    color: black;
}
.container-1 {
    display: flex;
    margin: 0 auto;
    width: 60%;
}
.main-section {
    background-color: rgba(245, 245, 245, 0.472);
    width: 60%;
    border: 1px solid rgba(80, 80, 80, 0.514);
    margin: 0 auto;
    margin-bottom: 50px;
    transform: translateY(20px);
}
.ordered-list .row-li {
    min-width: 100%;
    display: grid;
    grid-template-columns: 1fr 3fr 2.5fr 2.5fr 2.5fr 2.5fr;
    height: 85px;
    border-bottom: 1px solid rgba(131, 131, 131, 0.363);
}
.ordered-list li {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.robot-hand {
    width: 28%;
    position: absolute;
    z-index: -1;
    transform: translateY(-210px);
    background-repeat: no-repeat;
    user-select: none;
}

.sub-nav {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-bottom: 1px solid rgba(131, 131, 131, 0.363);;
    padding: 6px 24em;
    margin: 10px 0s;
    width: 100%;
    font-size: 0.8em;
}
.nav-div {
    margin: 0 8px;
}
.nav-info {
    color: rgb(71, 122, 250);
}
.logo {
    display: flex;
    align-items: center;
}

.header-div {
    padding-top: 20px;
    display: flex;
    flex-direction: column;
}
.title{
    font-size: 2em;
    margin-top: 20px;
    margin-left: 380px;
}

#searchBar {
    height: 30px;
    width: 180px;
    border-radius: 10px;
    margin-top: 10px;
    border: 1px solid black;
    padding: 5px;
}
#searchBar:focus {
    outline: none;
}

.top-row {
    border-bottom:  1px solid rgba(114, 114, 114, 0.76);
    display: grid;
    grid-template-columns: 1fr 2.5fr 3fr 2.5fr 2.5fr 2.5fr;
}
.top-row span {
    border-right:1px solid rgba(114, 114, 114, 0.63);
    padding: 8px 30px;   
    font-size: 1.1em;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.info-container {
    display: flex;
}

.info-box {
    border: 1px solid rgb(230, 230, 230);
    margin-left: 30px;
    margin-top: 15px;
    padding: 12px 18px;
    border-radius: 15px;
    box-shadow: 0px 5px 8px 3px rgba(0, 0, 0, 0.06);
}
.info-name {
    margin-bottom: 6px;
}

.row-li .rank {
    text-align: center;
}
.row-li span {
    text-align: center;
}
.row-li .name-symbol {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
}
.row-li .img-name {
    display: flex;
    margin-left: 10px;
}
.row-li .symbol {
    text-align: left;
    color: rgb(78, 78, 78);
    margin-top: 2px;
}
.row-li .name {
    text-align: left;
}
.market-graph {
    width: 100%;
    height: 75px;
}
.btn-group {
    display: flex;
    justify-content: center;
}
.highcharts-title {
    text-align: center;
}
.percent-change-red {
    color: #F1606D;
}
.percent-change-green {
    color: #33AD75;
}
.test {
    width: 35px;
    height: 35px;
}

footer {
    font-size: 1em;
    background-color:rgba(236, 233, 233, 0.5);
    width: 100%;
    padding: 10px;
    text-align: center;
    margin-top: 40px;
    position: relative;
    bottom: 0;
}
footer a {
    color: black;
}



.load-btn {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    color: #fff;
    background-color: #7896d6;
    border-color: #7896d6;
    transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    margin: 20px auto;
}

.trending-coins-div {
    display: flex;
    align-items: center;

    flex-wrap: wrap;
}

.trending-box {
    width: 250px;
    margin: 4px 0.5rem;
    padding: 1rem;
}

.main-nav {

    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid rgba(131, 131, 131, 0.363);
    padding: 6px 22em;
    width: 100%;
}


.nav-links {
    display: flex;
    justify-content: space-evenly;
    list-style: none;
}
.nav-links li {
    margin: 0 20px;

}



/* Categories */

.category-wrapper {
    width: 50%;
    background-color: rgba(245, 245, 245, 0.472);
    border: 1px solid rgba(80, 80, 80, 0.514);
    margin: 1em auto;
}

.category-header {
    border-bottom:  1px solid rgba(114, 114, 114, 0.76);
    display: grid;
    grid-template-columns: .5fr 2.2fr 1fr 1.5fr 1fr;
    padding: 0 2em;
}
.category-header span {
    padding: 0.35em ;
    font-weight: 500;
    text-align: center;
    display: flex;
}

.grid-styles { 
    display: grid;
    grid-template-columns:  .5fr 2.2fr 1fr 1.5fr 1fr;
    align-items: center;
    padding: 1em 2em ;
    border-bottom: 1px solid rgba(114, 114, 114, 0.404);
    }

.grid-styles span {
    padding: 0 .35em;
}



@media screen and (max-width: 1350px) {
    /* .robot-hand {
        transform: translateY(40px) !important;
    } */
}



@media screen and (max-width: 1200px) {
    .main-section {
        width: 90%;
    }
}
@media screen and (max-width: 1000px) {
    .robot-hand {
        display: none;
    }
    .container-1 {
        margin-left: 50px;
    }
}
@media screen and (max-width: 800px) {
    .info-box {
        display: none;
    }
    .container-1 {
        margin-left: 50px;
    }
    .main-section {
        width: 100vw;
    }
    .top-row {
        display: none;
    }
    nav {
        width: 100vw;
    }

    .nav-div:nth-child(5) {
        display: none;
    }
    .nav-div:nth-child(4) {
        display: none;
    }
}

@media screen and (max-width: 550px) {
    .ordered-list .row-li {
        grid-template-columns: 1fr 3fr 2.5fr 2.5fr;
    }
    .market-graph, .graph-link {
        display: none;
    }
    .row-li li:nth-child(6) {
        display: none;
    }
    .order-list .row-li {
        grid-template-columns: 1fr 3fr 2.5fr 2.5fr;
    }
    .market-cap {
        display: none;
    }
    .nav-div:nth-child(2) {
        display: none;
    }
    .row-li li:nth-child(5) {
        display: none;
    }
    .container-1 {
        margin: 0px;
        margin-left: 20px;
    }
    .header-div span,  nav, .footer, .row-li   {
        font-size: .8em;
    }
}

@media screen and (max-width: 430px) {
  
}

